import { Component, OnInit } from '@angular/core';
import { ResourceService } from 'src/app/config/resource.service';
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: 'app-memo',
  templateUrl: './memo.component.html',
  styleUrls: ['./memo.component.scss']
})
export class MemoComponent implements OnInit {
  notes = [];
  original;
  db = void 0;
  editingNoteContent = '';
  editingNoteId = void 0;
  passcode = void 0;
  httpError = '';
  selectedNumber = Number.MAX_SAFE_INTEGER;
  confirmDeletion = false;

  constructor(private resourceService: ResourceService,
        private spinner: NgxSpinnerService) { }

  ngOnInit() { }

  generateId () {
    return Math.floor(Math.random() * Number.MAX_SAFE_INTEGER);
  }

  getTitle (content: string) {
    return content.split("\n")[0];
  }

  editNote (id: number, index: number) {
    console.log('id', id);
    console.log('index', index);
    const note = this.notes.filter(note => note._id === id);
    this.selectedNumber = index;
    this.editingNoteId = id;
    this.editingNoteContent = note[0].content;
  }

  cancelUpdateNote () {
    this.editingNoteContent = '';
    this.editingNoteId = void 0;
    this.selectedNumber = Number.MAX_SAFE_INTEGER;
    this.confirmDeletion = false;
  }

  loadNotes () {
    this.spinner.show();
    this.httpError = void 0;
    
    this.resourceService.awsGetNotes(this.passcode).subscribe(
      result => {
        this.notes = [];
          result.forEach(element => {
            this.notes.push(element);
          });
          // this.notes = JSON.parse(result);
          this.original = this.deepClone(this.notes);
          this.spinner.hide();
      },
      error => {
          this.httpError = error.statusText;
          this.spinner.hide();
      }
    );

    this.spinner.hide();
  }

  addNote () {
    this.spinner.show();
    this.httpError = void 0;

    const body = `{
      "content": "${this.editingNoteContent}"
    }`;
    this.resourceService.awsAddNote(body, this.passcode).subscribe(
      result => {
        this.loadNotes();
        this.spinner.hide();
      },
      error => {
          this.httpError = error.statusText;
          this.spinner.hide();
      }
    );

    this.editingNoteContent = ''; 
    this.spinner.hide();
  }

  updateNote () {
    this.spinner.show();
    this.httpError = void 0;

    const body = `{
      "content": "${this.editingNoteContent}"
    }`;

    this.resourceService.awsUpdateNotes(this.editingNoteId, body, this.passcode).subscribe(
      result => {
        this.cancelUpdateNote();
        this.loadNotes();
        this.spinner.hide();
      },
      error => {
          this.httpError = error.statusText;
          this.spinner.hide();
      }
    );

    this.spinner.hide();
  }
  
  deleteNote () {
    this.spinner.show();
    this.httpError = void 0;

    this.resourceService.awsDeleteNote(this.editingNoteId, this.passcode).subscribe(
      result => {
        this.cancelUpdateNote();
        this.loadNotes();
        this.spinner.hide();
      },
      error => {
          this.httpError = error.statusText;
          this.spinner.hide();
      }
    );
  
    this.spinner.hide();
  }

  deepClone (obj) {
    return JSON.parse(JSON.stringify(obj));
  }

  setKeypass (keypass: string) {
    this.passcode = keypass;
    this.loadNotes();
  }

  clearPasscode () {
    this.passcode = void 0;
    this.httpError = void 0;
  }
  
  toggleDeleteFlag () {
    this.confirmDeletion = !this.confirmDeletion;
  }

  search (event) {
    // event.target.value
    if (event.target.value) {
        this.notes = this.notes.filter(note => note.content.toLowerCase().includes(event.target.value.toLowerCase()));
        event.target.value = '';
    } else {
        this.notes = this.deepClone(this.original);
    }
  }
}
