import { Component, OnInit } from '@angular/core';
import { ResourceService } from 'src/app/config/resource.service';
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: 'app-notes',
  templateUrl: './notes.component.html',
  styleUrls: ['./notes.component.scss']
})
export class NotesComponent implements OnInit {
  notes;
  original;
  editingNoteContent = '';
  editingNoteId = void 0;
  cheGist = 'https://api.github.com/gists/973de2c3eb3f55518e97a1d3d18eb79e';
  gistFile = 'notes.json';
  passcode = void 0;
  httpError = '';
  selectedNumber = Number.MAX_SAFE_INTEGER;
  confirmDeletion = false;

  constructor(private resourceService: ResourceService,
        private spinner: NgxSpinnerService) { }

  ngOnInit() { }

  generateId () {
    return Math.floor(Math.random() * Number.MAX_SAFE_INTEGER);
  }

  getTitle (content: string) {
    return content.split("\n")[0];
  }

  editNote (id: number, index: number) {
    const note = this.notes.filter(note => note.id === id);
    this.selectedNumber = index;
    this.editingNoteId = id;
    this.editingNoteContent = note[0].content;
  }

  cancelUpdateNote () {
    this.editingNoteContent = '';
    this.editingNoteId = void 0;
    this.selectedNumber = Number.MAX_SAFE_INTEGER;
    this.confirmDeletion = false;
  }

  loadNotes () {
    this.spinner.show();
    this.httpError = void 0;
    this.resourceService.getGist(this.cheGist, this.passcode).subscribe(
        result => {
            this.notes = JSON.parse(result[this.gistFile]['content']).data;
            this.original = this.deepClone(this.notes);
            this.spinner.hide();
        },
        error => {
            this.httpError = error.statusText;
            this.spinner.hide();
        }
    );
  }

  addNote () {
    this.spinner.show();
    this.httpError = void 0;
    const newEntry = {
      id: this.generateId(),
      content: this.editingNoteContent
    }
    this.notes.push(newEntry);
    this.resourceService.patchGist(this.gistFile, this.cheGist, this.notes, this.passcode).subscribe(
      (val) => {
        console.log("PATCH call successful value returned in body", val);
        this.spinner.hide();
      },
      error => {
        this.httpError = error.statusText;
        this.spinner.hide();
      },
      () => {
        console.log("The PATCH observable is now completed.");
    });
    this.editingNoteContent = ''; 
  }

  updateNote () {
    this.spinner.show();
    this.httpError = void 0;
    if (this.editingNoteId) {
      this.notes.map(note => 
        note.id === this.editingNoteId ? note.content = this.editingNoteContent : note
      );
      this.resourceService.patchGist(this.gistFile, this.cheGist, this.notes, this.passcode).subscribe(
        (val) => {
          console.log("PATCH call successful value returned in body", val);
          this.spinner.hide();
        },
        error => {
          this.httpError = error.statusText;
          this.spinner.hide();
        },
        () => {
          console.log("The PATCH observable is now completed.");
        });
    }
  }
  
  deleteNote () {
    this.spinner.show();
    
    this.httpError = void 0;
    this.notes = this.notes.filter(note => note.id !== this.editingNoteId);
    this.resourceService.patchGist(this.gistFile, this.cheGist, this.notes, this.passcode).subscribe(
        (val) => {
            console.log("PATCH call successful value returned in body", val);
            this.selectedNumber = Number.MAX_SAFE_INTEGER;
            this.editingNoteId = void 0;
            this.editingNoteContent = void 0;
            this.cancelUpdateNote();
        },
        error => {
            this.httpError = error.statusText;
            this.spinner.hide()
        },
        () => {
            console.log("The PATCH observable is now completed.");
    });
  
    this.spinner.hide();
  }

  deepClone (obj) {
    return JSON.parse(JSON.stringify(obj));
  }

  setKeypass (keypass: string) {
    this.passcode = keypass;
    this.loadNotes();
  }

  clearPasscode () {
    this.passcode = void 0;
    this.httpError = void 0;
  }
  
  toggleDeleteFlag () {
    this.confirmDeletion = !this.confirmDeletion;
  }

  search (event) {
    // event.target.value
    if (event.target.value) {
        this.notes = this.notes.filter(note => note.content.toLowerCase().includes(event.target.value.toLowerCase()));
        event.target.value = '';
    } else {
        this.notes = this.deepClone(this.original);
    }
  }
}
